// eslint-disable-next-line no-undef
class QtQuick_RegExpValidator extends QtQml_QtObject {
  static properties = {
    regExp: "var"
  };

  constructor(meta) {
    super(meta);
    this.parent = meta.parent
    this.regExpChanged.connect(this, this.$regExpChanged)
    //this.$parent.stops.push({position: this.position, color: this.color})
  }
  $regExpChanged(){
    //this.parent.impl.pattern = this.regExp
  }
  validate(string) {
    if (!this.regExp) return true;
    return this.regExp.test(string);
  }
}
QmlWeb.registerQmlType(QtQuick_RegExpValidator);
