// eslint-disable-next-line no-undef
class QtQuick_TextInput extends QtQuick_Item {
  static enums = {
    TextInput: { 
      Normal: 0, 
      Password: 1, 
      NoEcho: 2, 
      PasswordEchoOnEdit: 3,
      AlignLeft: Qt.AlignLeft,
      AlignRight: Qt.AlignRight,
      AlignHCenter: Qt.AlignHCenter,
      AlignTop: Qt.AlignTop,
      AlignBottom: Qt.AlignBottom,
      AlignVCenter: Qt.AlignVCenter, 
      NoWrap: 0,
      WordWrap: 1,
      WrapAnywhere: 2,
      Wrap: 3,
    }
  };
  static properties = {
    color: { type: "color", initialValue: "white" },
    text: "string",
    font: "font",
    maximumLength: { type: "int", initialValue: -1 },
    readOnly: { type: "bool", initialValue: false },
    validator: "var",
    echoMode: "enum", // TextInput.Normal
    wrapMode: { type: "enum", initialValue: 0 }, // TextInput.Normal
    horizontalAlignment: "enum",
    verticalAlignment: { type: "enum", initialValue: Qt.AlignTop },
  };
  static signals = {
    accepted: []
  };

  constructor(meta) {
    super(meta);
    const input = this.impl = document.createElement("textarea");
    input.type = "text";
    input.disabled = true;
    input.style.pointerEvents = "auto";
    // In some browsers text-inputs have a margin by default, which distorts
    // the positioning, so we need to manually set it to 0.
    input.style.margin = "0";
    input.style.padding = "0";
    input.style.width = "100%";
    input.style.height = "100%";
    input.style.border = "none";
    input.style.outline = "none";
    input.style.background = "none";
    input.style.resize = "none";
    input.style.wordBreak = 'normal'
    input.style.whiteSpace = 'nowrap'
    input.style.overflow = 'hidden'
    /*
    input.style.display = "table-cell";
    input.contentEditable = true;
    input.style.whiteSpace = "nowrap";
    input.style.whiteSpace = "nowrap";
    input.style.overflowX = "hidden";
    */
    this.dom.appendChild(input);
    this.setupFocusOnDom(input);
    input.disabled = false;

    this.Component.completed.connect(this, this.Component$onCompleted);
    this.textChanged.connect(this, this.$onTextChanged);
    this.echoModeChanged.connect(this, this.$onEchoModeChanged);
    this.maximumLengthChanged.connect(this, this.$onMaximumLengthChanged);
    this.readOnlyChanged.connect(this, this.$onReadOnlyChanged);
    this.horizontalAlignmentChanged.connect(this, this.$alignChanged);
    this.verticalAlignmentChanged.connect(this, this.$alignChanged);

    this.heightChanged.connect(this, this.$heightChanged);
    this.widthChanged.connect(this, this.$widthChanged);
    this.colorChanged.connect(this, this.$colorChanged);
    this.validatorChanged.connect(this, this.$validatorChanged);
    this.wrapModeChanged.connect(this, this.$wrapModeChanged);
    //this.Keys.pressed.connect(this, this.Keys$onPressed);

    this.impl.addEventListener("input", () => this.$updateValue());

    this.impl.addEventListener("keydown", (e) => {this.Keys$onPressed(e)});

    this._buffer == null;
  }
  Component$onCompleted() {
    this.implicitWidth = this.impl.offsetWidth;
    this.implicitHeight = this.impl.offsetHeight;

  }
  $wrapModeChanged(){
    
    switch(this.wrapMode){
      case TextInput.NoWrap:
        this.impl.style.wordBreak = 'normal'
        this.impl.style.whiteSpace = 'nowrap'
        break;
      case TextInput.WordWrap:
        this.impl.style.wordBreak = 'break-word'
        this.impl.style.whiteSpace = 'break-spaces'
        break;
      case TextInput.WrapAnywhere:
        this.impl.style.wordBreak = 'break-all'
        this.impl.style.whiteSpace = 'break-spaces'
        break;
      case TextInput.Wrap:
        this.impl.style.wordBreak = 'break-all'
        this.impl.style.whiteSpace = 'break-spaces'
        break;
    }
  }
  $colorChanged(newVal){
    this.impl.style.color = newVal.$css;
  }
  $validatorChanged(){
    //this.impl.pattern
  }
  $heightChanged(){
    this.impl.style.height = `${this.height}px`;
    this.$alignChanged();
  }
  $widthChanged(){
    this.impl.style.width = `${this.width}px`;
    this.$alignChanged();
  }
  $alignChanged(){

    switch(this.horizontalAlignment){
      case TextInput.AlignLeft:
        this.impl.style.textAlign = 'start';
        break;
      case TextInput.AlignRight:
        this.impl.style.textAlign = 'end';
        break;
      case TextInput.AlignHCenter:
        this.impl.style.textAlign = 'center';
        break;
    }

    this.impl.style.padding = `0`;
    this.impl.style.height = `${this.height}px`;
    switch(this.verticalAlignment){
      case TextInput.AlignTop:
        //this.impl.style.paddingBottom = `${this.height - this.font.pixelSize}px`;
        //this.impl.style.height = `auto`;
        break;
      case TextInput.AlignBottom:
        this.impl.style.paddingTop = `${this.height - this.font.pixelSize}px`;
        break;
      case TextInput.AlignVCenter:
        this.impl.style.paddingTop = `${this.height/2 - this.countLines()*this.font.pixelSize/2}px`;
        break;
    }

  }
  $onTextChanged(newVal) {
    // We have to check if value actually changes.
    // If we do not have this check, then after user updates text input
    // following occurs: user updates gui text -> updateValue gets called ->
    // textChanged gets called -> gui value updates again -> caret position
    // moves to the right!
    if (this.impl.value !== newVal) {
      this.impl.value = newVal;
    }
    this.$alignChanged()
  }
  $onEchoModeChanged(newVal) {
    const TextInput = this.TextInput;
    const input = this.impl;
    switch (newVal) {
      case TextInput.Normal:
        input.type = "text";
        break;
      case TextInput.Password:
        input.type = "password";
        break;
      case TextInput.NoEcho:
        // Not supported, use password, that's nearest
        input.type = "password";
        break;
      case TextInput.PasswordEchoOnEdit:
        // Not supported, use password, that's nearest
        input.type = "password";
        break;
    }
  }
  $onMaximumLengthChanged(newVal) {
    this.impl.maxLength = newVal < 0 ? null : newVal;
  }
  $onReadOnlyChanged(newVal) {
    this.impl.disabled = newVal;
  }
  Keys$onPressed(e) {
    
    
    const Qt = QmlWeb.Qt;
    const submit = e.keyCode === Qt.Key_Return || e.keyCode === Qt.Key_Enter;
    if (submit && this.$testValidator()) {
      e.preventDefault();
      this.accepted();
      
      e.accepted = true;
    }
  }

  $testValidator() {
    if (this.validator) {
      return this.validator.validate(this.text);
    }
    return true;
  }
  countLines() {
    let textarea = this.impl
      if (this._buffer == null) {
          this._buffer = document.createElement('textarea');
          this._buffer.style.border = 'none';
          this._buffer.style.height = '0';
          this._buffer.style.overflow = 'hidden';
          this._buffer.style.padding = '0';
          this._buffer.style.position = 'absolute';
          this._buffer.style.left = '0';
          this._buffer.style.top = '0';
          this._buffer.style.zIndex = '-1';
          this.dom.appendChild(this._buffer);
      }

      var cs = window.getComputedStyle(textarea);
      var pl = parseInt(cs.paddingLeft);
      var pr = parseInt(cs.paddingRight);
      var lh = parseInt(cs.lineHeight);

      // [cs.lineHeight] may return 'normal', which means line height = font size.
      if (isNaN(lh)) lh = parseInt(cs.fontSize);

      // Copy content width.
      this._buffer.style.width = (textarea.clientWidth - pl - pr) + 'px';

      // Copy text properties.
      this._buffer.style.font = cs.font;
      this._buffer.style.letterSpacing = cs.letterSpacing;
      this._buffer.style.whiteSpace = cs.whiteSpace;
      this._buffer.style.wordBreak = cs.wordBreak;
      this._buffer.style.wordSpacing = cs.wordSpacing;
      this._buffer.style.wordWrap = cs.wordWrap;

      // Copy value.
      this._buffer.value = textarea.value;

      var result = Math.floor(this._buffer.scrollHeight / lh);
      if (result == 0) result = 1;
      return result;
  }
  $updateValue() {
    if (this.text !== this.impl.value) {
      this.$canEditReadOnlyProperties = true;
      if(this.validator) {
        if(this.impl.value == '' || this.validator.validate(this.impl.value)) 
        {
          this.text = this.impl.value; 
        } else {
          this.impl.value = this.text; 
        }
      } else {
        this.text = this.impl.value; 
      }
      this.$canEditReadOnlyProperties = false;
    }
  }
}
QmlWeb.registerQmlType(QtQuick_TextInput);
