// eslint-disable-next-line no-undef
class QtGraphicalEffects_DropShadow extends QtQuick_Item {
    static properties = {
      cached: "bool",
      color: { type: "color", initialValue: "black" },
      horizontalOffset: "real",
      radius: { type: "int", initialValue: 4 },
      samples: { type: "int", initialValue: 9 },
      source: { type: "var", initialValue: null },
      spread: { type: "real", initialValue: 0 },
      transparentBorder: "bool",
      verticalOffset: "real",
    };
  
    constructor(meta) {
      super(meta);
      this.colorChanged.connect(this, this.$updateBoxShadow);
      this.samplesChanged.connect(this, this.$updateBoxShadow);
      this.radiusChanged.connect(this, this.$updateBoxShadow);
      this.spreadChanged.connect(this, this.$updateBoxShadow);
      this.sourceChanged.connect(this, this.$updateBoxShadow);
      this.verticalOffsetChanged.connect(this, this.$updateBoxShadow);
      this.horizontalOffsetChanged.connect(this, this.$updateBoxShadow);
    }
    $updateBoxShadow() {
      const { color, horizontalOffset, verticalOffset, radius, spread } = this
      if(this.source) {
        const style = this.source.impl.style
        style.boxShadow = `${horizontalOffset}px ${verticalOffset}px ${radius}px ${spread}px ${color}`
      }
    }
  }
  QmlWeb.registerQmlType(QtGraphicalEffects_DropShadow);
