// eslint-disable-next-line no-undef
class QtQuick_Gradient extends QtQuick_Item {
    static properties = {
        orientation: "enum",
        stops: "list"
    };
    static defaultProperty = "stops";
  
    constructor(meta) {
        super(meta);
        this.Gradient = {
            Vertical: Qt.Vertical,
            Horizontal: Qt.Horizontal
        }
        
        const bg = this.impl = document.createElement("div");
        bg.style.pointerEvents = "none";
        bg.style.position = "absolute";
        bg.style.background = "none";
        bg.style.left = bg.style.right = bg.style.top = bg.style.bottom = "0px";

        let firstChild = this.$parent.dom.firstChild;
        if(firstChild && firstChild.nextSibling){
            this.$parent.dom.insertBefore(bg, firstChild.nextSibling);
        } else {
            this.$parent.dom.appendChild(bg);
        }

        this.stopsChanged.connect(this, this.$stopsChanged)
        this.orientationChanged.connect(this, this.$orientationChanged)
    }
    applyGradient(){
        let g_temp = []
        //old_pos = 0;
        for(let i = 0; i < this.stops.length; i++){
            g_temp.push(`${this.stops[i].color} ${this.stops[i].position*100}%`)
        }

        let g_str = 'none'
        if(this.orientation === Qt.Vertical){
            g_str = `linear-gradient(180deg, ${g_temp.join(',')})` 
        } else if(this.orientation === Qt.Horizontal){
            g_str = `linear-gradient(90deg, ${g_temp.join(',')})`
        }
        
        this.impl.style.background = g_str;
    }
    $stopsChanged(){
        this.applyGradient()
    }
    $orientationChanged(){
        this.applyGradient()
    }

  }
  QmlWeb.registerQmlType(QtQuick_Gradient);
